  .example {
    &:not(:first-child) {
      margin-top: 1.5rem;
    }

    .tab-content {
      background-color: $light-50 !important;

      @at-root .dark-theme & {
        background-color: rgba(255, 255, 255, .1) !important;
      }
    }

    code[class*="language-"],
    pre[class*="language-"] {
      font-size: .875rem !important;
    }

    :not(pre) > code[class*="language-"],
    pre[class*="language-"] {
      background: transparent;
    }

    & + p {
      margin-top: 1.5rem
    }

    // Components examples
    .preview,
    .preview .col {
      + p {
        margin-top: 2rem;
      }

      > .form-control {
        + .form-control {
          margin-top: .5rem;
        }
      }

      > .nav + .nav,
      > .alert + .alert,
      > .navbar + .navbar,
      > .progress + .progress {
        margin-top: 1rem;
      }

      > .dropdown-menu {
        position: static;
        display: block;
      }

      > :last-child {
        margin-bottom: 0;
      }

      // Images
      > svg + svg,
      > img + img {
        margin-left: .5rem;
      }

      // Buttons
      > .btn,
      > .btn-group {
        margin: .25rem .125rem;
      }
      > .btn-toolbar + .btn-toolbar {
        margin-top: .5rem;
      }

      // List groups
      > .list-group {
        max-width: 400px;
      }

      > [class*="list-group-horizontal"] {
        max-width: 100%;
      }

      // Navbars
      .fixed-top,
      .sticky-top {
        position: static;
        margin: -1rem -1rem 1rem;
      }

      .fixed-bottom {
        position: static;
        margin: 1rem -1rem -1rem;
      }

      @include media-breakpoint-up(sm) {
        .fixed-top,
        .sticky-top {
          margin: -1.5rem -1.5rem 1rem;
        }
        .fixed-bottom {
          margin: 1rem -1.5rem -1.5rem;
        }
      }

      // Pagination
      .pagination {
        margin-top: .5rem;
        margin-bottom: .5rem;
      }
    }
  }
